






import { Component, Vue } from "vue-property-decorator";
import { getModule } from "vuex-module-decorators";
import PhotoStore from "store/modules/photo/PhotoStore";
import msg from "common/MessageUtils";
import Time from "common/mixin";
import VueLazyload from "vue-lazyload";
Vue.use(VueLazyload, {
  preLoad: 1.3,
  attempt: 1
});

@Component({})
export default class Photo extends Vue {
  private store: any;
  private searchTime: [Date, Date] | null = null; // 
  private selectType: number | null = null; // 
  private selectTypeList: any[] = [
    {
      label: "",
      value: 1
    },
    {
      label: "",
      value: 2
    }
  ]; // 

  private selectState: number | null = null; // 
  private selectStateList: any[] = [
    {
      label: "",
      value: 0
    },
    {
      label: "",
      value: 1
    },
    {
      label: "",
      value: 2
    }
  ]; // 

  private selectPeople: number | null = null; // 
  private imgList: any[] = []; // 
  private pageNum = 1; // 
  private pageSize = 20; // 
  private dialog = false; // 
  private imgData: ImgData = {
    url: "", // 
    inspect_task: "", // 
    create_on: "", // 
    hidden_level: "", // 
    name: "", // 
    area_name: "", // 
    create_name: "", // 
  }; // 

  private infiniteScroll: any = {}; // 
  constructor() {
    super();
    this.store = getModule(PhotoStore);
  }

  mounted() {
    this.createScroll();
    this.getImgList();
  }

  // 
  scroll(e: any) {
    const top = (e.target as HTMLElement).scrollTop - 82
    const clientH = (e.target as HTMLElement).clientHeight
    const scrollH = (this.$refs.scroll as HTMLElement).clientHeight;
    if (scrollH - (top + clientH) <= 200) {
      if (this.infiniteScroll) {
        clearTimeout(this.infiniteScroll)
      }
      this.infiniteScroll = setTimeout(() => {
        this.pageNum++;
        this.getImgList();
        console.log("");
      }, 300);
    }
  }

  // 
  getImgList() {
    const params = {
      projectId: sessionStorage.getItem("seeProjectId"),
      startTime:
        this.searchTime && this.searchTime.length
          ? new Time(this.searchTime[0]).get()
          : null,
      endTime:
        this.searchTime && this.searchTime.length
          ? new Time(this.searchTime[1]).get()
          : null,
      hidden_level: this.selectType,
      hidden_status: this.selectState,
      create_name: this.selectPeople,
      pageNum: this.pageNum,
      pageSize: this.pageSize
    };
    this.store.getImgList(params).then((res: any) => {
      if (res.code === 0 && res.data.list && res.data.list.length) {
        const arr: any[] = []
        res.data.list.forEach((item: any) => {
          const child = item.rectificationReviewRecord;
          if (child && child.length) {
            item.inspect_id = item.id;
            arr.push({
              index: !arr.length
                ? 0
                : arr[arr.length - 1].index + child.length + 1,
              data: child
            });
          }
        });
        arr.forEach(item => {
          res.data.list.splice(item.index + 1, 0, ...item.data);
        });
        this.imgList.push(...res.data.list);
      } else {
        if (this.imgList.length) {
          msg.warning("");
        }
        this.clearScroll()
      }
    });
  }

  // 
  getImgData(imgData: any) {
    this.store
      .getImgData({ projectId: imgData.inspect_id })
      .then((res: any) => {
        if (res.code === 0) {
          this.dialog = true;
          this.imgData = {
            url: imgData.photo, // 
            inspect_task: res.data.inspect_task, // 
            create_on: res.data.create_on, // 
            hidden_level: res.data.hidden_level, // 
            name: res.data.name, // 
            area_name: res.data.area_name, // 
            create_name: res.data.create_name // 
          };
        }
      });
  }

  // 
  search() {
    this.createScroll()
    this.imgList = []
    this.pageNum = 1
    this.getImgList()
  }

  // 
  createScroll() {
    document
      .getElementsByClassName("router-wrap")[0]
      .addEventListener("scroll", this.scroll, false);
  }

  // 
  clearScroll() {
    document
      .getElementsByClassName("router-wrap")[0]
      .removeEventListener("scroll", this.scroll, false);
  }

  beforeDestroy() {
    this.clearScroll()
  }
}
interface ImgData {
  url: string; // 
  inspect_task: string; // 
  create_on: string; // 
  hidden_level: string; // 
  name: string; // 
  area_name: string; // 
  create_name: string; // 
}
